.movie {
    width: 364px;
    height: 271px;
    margin: 0;
    background-color: #fff;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.08);
    display: grid;
    grid-template-columns: 306px 58px;
    grid-template-rows: 32px 36px 203px;
    grid-template-areas:
        "title button"
        "duration button"
        "poster poster";
}

.movie__title {
    grid-area: title;
    margin: 0 0 0 14px;
    padding: 14px 0 0;
    box-sizing: border-box;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
}

.movie__duration {
    grid-area: duration;
    margin: 0 0 0 14px;
    padding: 9px 0 14px;
    box-sizing: border-box;
    font-style: normal;
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    color: #A0A0A0;
}

.movie__button {
    grid-area: button;
    margin: 0;
    padding: 19px 14px;
    box-sizing: border-box;
    border: none;
    background-color: #fff;
    cursor: pointer;
}

.movie__button:hover {
    opacity: .7;
}

.movie__button-img {
    grid-area: button;
    margin: 0;
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.movie__poster {
    grid-area: poster;
    margin: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media screen and (min-width: 750px) and (max-width: 1279px) {
    .movie {
        width: 339px;
        height: 258px;
        grid-template-columns: 281px 58px;
        grid-template-rows: 32px 36px 190px;
    }
}

@media screen and (min-width: 320px) and (max-width: 750px) {
    .movie {
        width: 300px;
        height: 236px;
        grid-template-columns: 242px 58px;
        grid-template-rows: 32px 36px 168px;
    }
}