.login {
    margin: 0;
    height: 100vh;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.login__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.login__logo {
    margin: 0;
    display: flex;
}

.login__img {
    width: 38px;
    height: 38px;
}

.login__title {
    margin: 40px 0;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    text-align: left;
}

.login__form {
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__label {
    margin: 6px 0 0;
    width: 396px;
    height: 44px;
    border-bottom: 1px solid #E8E8E8;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.login__label:focus-within {
    border-bottom: 1px solid #2BE080;
    ;
}

.login__subtitle {
    margin: 0 0 6px;
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: #A0A0A0;
}

.login__input {
    padding: 0;
    margin: 0 0 10px;
    border: none;
    outline: none;
    text-align: left;
    font-style: normal;
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
}

.login__type-input-error {
    display: block;
    height: 12px;
    margin: 6px 0 0;
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: #EE3465;
    text-align: center;
}

.login__submit {
    margin: 100px 0 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__submit_type-error {
    display: block;
    height: 13px;
    margin: 0 0 20px;
    font-style: normal;
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    text-align: center;
    color: #FF004C;
}

.login__submit-button {
    width: 396px;
    height: 45px;
    background-color: #2BE080;
    border-radius: 3px;
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #fff;
    cursor: pointer;
    border: none;
}

.login__submit-button:hover {
    opacity: .7;
}

.login__submit-button_inactive {
    background-color: #F8F8F8;
    color: #C2C2C2;
}

.login__signin {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login__signin-text {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #A0A0A0;
}

.login__signin-link {
    text-decoration: none;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #2BE080;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
    .login__logo {
        justify-content: center;
    }

    .login__title {
        text-align: center;
    }

    .login__label {
        width: 260px;
    }

    .login__submit-button {
        width: 260px;
        font-size: 12px;
        line-height: 15px;
    }

    .login__signin-text {
        font-size: 12px;
        line-height: 15px;
    }
}